/* Main */
#main {
  background-color: #FFF;

  > * {
    border-top: solid 0px _palette(border);

    &:first-child {
      border-top: 0;
    }

    @include inner;
  }

  &.alt {
    background-color: transparent;
    border-bottom: 0px;
  }
}

