/* Form */

form {
	margin: 20px 0 0 0; /* Adds 20px margin above the form */
	    padding-top: 20px;  /* Adds 20px padding inside the form at the top */

    .field {
        margin: 0 0 (_size(element-margin) * 1) 0;

        &.half {
            width: 50%;
            float: left;
            padding: 0 0 0 (_size(element-margin) * 1 * 0.5);

            &.first {
                padding: 0 (_size(element-margin) * 1 * 0.5) 0 0;
            }
        }
    }

    > .actions {
        margin: (_size(element-margin) * 1.25) 0 0 0 !important;
    }

    @include breakpoint(small) {
        .field {
            margin: 0 0 (_size(element-margin) * 0.75) 0;

            &.half {
                padding: 0 0 0 (_size(element-margin) * 0.75 * 0.5);

                &.first {
                    padding: 0 (_size(element-margin) * 0.75 * 0.5) 0 0;
                }
            }
        }

        > .actions {
            margin: (_size(element-margin) * 1) 0 0 0 !important;
        }
    }

    @include breakpoint(xsmall) {
        .field {
            &.half {
                width: 100%;
                float: none;
                padding: 0;

                &.first {
                    padding: 0;
                }
            }
        }
    }
}

label {
    color: _palette(fg-bold);
    display: block;
    font-size: 0.8em;
    font-weight: _font(weight-bold);
    letter-spacing: _font(letter-spacing-alt);
    margin: 0 0 (_size(element-margin) * 0.5) 0;
    text-transform: uppercase;
}

/* Inputs with bottom border only */
input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
select {
    @include vendor('appearance', 'none');
    background: none;
    border: none;
    border-bottom: 2px solid #ccc; /* Bottom border only */
    color: inherit;
    display: block;
    outline: 0;
    padding: 0.5em 0;
    text-decoration: none;
    width: 100%;
    transition: border-color 0.3s ease-in-out;

    &:focus,
    &:hover {
        border-bottom-color: #333;
    }
}

/* Full border for textarea */
textarea {
    @include vendor('appearance', 'none');
    background: _palette(border-bg);
    border: 1px solid #ccc;
    color: inherit;
    display: block;
    outline: 0;
    padding: 0.75em 1em;
    text-decoration: none;
    width: 100%;
    transition: border-color 0.3s ease-in-out;

    &:focus,
    &:hover {
        border-color: #333;
    }
}

.select-wrapper {
    @include icon;
    display: block;
    position: relative;

    &:before {
        color: _palette(border);
        content: '\f078';
        display: block;
        height: _size(element-height);
        line-height: _size(element-height);
        pointer-events: none;
        position: absolute;
        right: 0;
        text-align: center;
        top: 0;
        width: _size(element-height);
    }

    select::-ms-expand {
        display: none;
    }
}

input[type="text"],
input[type="password"],
input[type="email"],
select {
    height: _size(element-height);
}

/* Placeholder Styles */
::-webkit-input-placeholder {
    color: _palette(fg-light) !important;
    opacity: 1.0;
}

:-moz-placeholder {
    color: _palette(fg-light) !important;
    opacity: 1.0;
}

::-moz-placeholder {
    color: _palette(fg-light) !important;
    opacity: 1.0;
}

:-ms-input-placeholder {
    color: _palette(fg-light) !important;
    opacity: 1.0;
}

.formerize-placeholder {
    color: _palette(fg-light) !important;
    opacity: 1.0;
}

/* Substack embed form: force full width and remove left margin */
/* Make the Substack block expand in the right column */


.contact-method {
  max-width: 100% !important;
  width: 100% !important;
  box-sizing: border-box;
}
.contact-method iframe {
  width: 100% !important;
  min-width: 0 !important;
  max-width: 100% !important;
  display: block;
  box-sizing: border-box;
}



.substack-form {
  display: flex;
  gap: 0.5rem;
  max-width: 400px;
  margin: 2rem 0;

  input[type="email"] {
    flex: 1;
    padding: 0.75rem 1rem;
    border: 1px solid map-get($palette, 'border');
    font-family: 'Karla', sans-serif;
    font-size: 1rem;
    border-radius: 0; // keep crisp
  }

  button {
    background: map-get($palette, 'brand-color');
    color: #fff;
    padding: 0.75rem 1.25rem;
    border: none;
    font-family: 'Karla', sans-serif;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 0;

    &:hover {
      background: map-get($palette, 'spicy');
    }
  }
}
